.Topbar {
  position: absolute;
  left: 0;
  right: 0;
  margin-inline: auto;
  width: max-content;
  height: 110px;
  border-radius: 0 0 3em 3em;
  background-color: hsla(0, 0%, 16%, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.108);
  color: whitesmoke;
  padding: 0 3em 0 4em;
  backdrop-filter: blur(0.8px);
  z-index: 10;

  display: flex;
  justify-content: center;
  gap: 4em;
}

.icon-div {
  width: 2vw;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: space-around;
  transition: color .2s ease-in-out;
}
.icon-div:hover {
  color: orange;
  cursor: pointer;
}


